    position: fixed;
    left: 0;
    top: 0;
    width: 300px;
    height: 100vh;
    background: #ffffff;
    z-index: 3;
    transition: transform 0.3s ease-in-out;
    transform: translateX(-100%);
    display: none;
  }
  .header-menu-container.container.active {
    left: 0;
